@media screen and (max-width:480px) {
  @import 'common';

  /*  */
  .header {
    font-size: 20px;
  }

  /*  */
  .info-text {
    font-size: 16px;
    padding: 0px !important;
    margin: 0;
  }

  /*  */
  .card-content {
    font-size: 14px;
  }

  /*  */
  .card-image {
    padding: 0px !important;
  }

  /*  */
  .card-point {
    color: $c-red;
    font-size: 16px;
  }

  .card-pointDetail {
    font-size: 12px;
  }

  /*  */
  /*  */
  .cassette-mark {
    display: inline-block;
    width: 24px;

    &::after {
      font-size: 16px;
    }
  }

  /*  */
  .footer-icon::after {
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    font-size: 24px;
  }
}